h1{
  text-align: center;
  color: rgb(20, 61, 209);
}
.form-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

.form-input {
  margin-left: 45px;
  height: 20px;
    width: 46%;
    padding-left: 15px;
    border: 1px soild #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;
}

.form-textarea {
  margin-left: 10px;
  width: 50%;
  margin-bottom: -10px;
}

.form-buttons button{
  margin-left: 20px;
  width: 40%;
  height: 42px;
  color: white;
  background: #ff4141;
  margin-top: 30px;
  border: none;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
}


h2, h3{
  text-align: center;
  font-size: 25px;
}
p{
  text-align: center;
  font-weight: bold;
  font-size: 25px;
}
.btn{
  display: flex;
  margin: auto;
  width: 6%;
  height: 35px;
  color: white;
  background: #ff4141;
  margin-top: 30px;
  border: none;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
}
